import {useState} from "react";
import AutoSug from './AutoS/AutoSug';
import db from "./db";
import './App.css';
import List from './List';

function App() {
    const [name, setName] = useState('No Entry');
    const [val,setVal] = useState('');
    const [lan,setLan] = useState('Konkani');

    const handleClear = (e) =>{
      setVal('');
      setName('No Entry');
      document.getElementById('clear').value='';
      document.getElementById('clear').focus();
    }
    

  const handleChange = (e) =>{
      var s =e.target.value;
      s=s.split(" ");
      const ks=s[0];
      const data = db.find((d) => d.E === ks.toLowerCase());
      
      if(data){
        setVal(data.E);
        if(lan==="")
          setName(data.H);
        else if(lan==="")
          setName(data.M);
        else  
          setName(data.K);
      }
      else{
        setName('No Entry');
        setVal('');
      }
    }
  
const handleClick = (e) =>{
    var x = e.target.value;
    const data = db.find((d) => d.E === val);
    if(data){
      if(x==="H"){
        setLan('');
        setName(data.H);
      }
        else if(x==="M")
        {
          setLan('');
          setName(data.M);
        }
        else{
          setName(data.K);
          setLan('Konkani');
        }
    }
    else{
      setName('No Entry')
    }

  }

  return (
    <div className="App">
      <h1>Translate to {lan}</h1>
      <h5>Enter the word present in the list and choose any languange from dropdown to see the translation.</h5>
      <div className="Box">
     <input id="clear" onChange={handleChange} placeholder="Enter word to Translate" type="text" ></input>
     <button onClick={handleClear}>X</button>
      <select name="Language" onChange={handleClick}>
        <option>English</option>
        <option value="H">Hindi</option>
        <option value="M">Malayalam</option>
      </select>
      <label>{lan}<p>{name}</p> </label>
      </div>
    <span>Check if word is present in List</span>
      <AutoSug />
      <List />
      <h4>Created for Learning</h4>
      <h4>For feedback @ ajaypanand007@gmail.com</h4>
    </div>
  );
  }

export default App;
